<template>
  <div class="view"></div>
</template>

<script>
import { getErrors } from "../api";
import { mapGetters } from "vuex";
export default {
  name: "View issues",
  mounted() {
    !this.viewerReady && this.$router.push({ name: "/" });
    this.viewerReady && this.getErrorsFromApi()
  },
  computed: {
    ...mapGetters(["getFile", "siteUrl", "viewerReady"])
  },
  beforeRouteUpdate(from, to, next) {
    if (!this.viewerReady) {
      next(false);
      this.$router.push({ name: "/" });
    } else {
      next(true);
      this.getErrorsFromApi()
    }
  },
  methods: {
    getErrorsFromApi() {
      const self = this;
      const nodes = document.querySelector(".site-viewer iframe").contentWindow.document.body.getElementsByTagName("*");
      getErrors(this.getFile, nodes, errors => {
        if (!errors || !Array.isArray(errors)) {
          return
        }
        self.$store.dispatch("setErrors", errors);
      });
    }
  }
};
</script>

<style scoped></style>
