import Vue from "vue";
import Vuex from "vuex";
import config from "./config";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    getFile: null,
    siteUrl: null,
    errors: null
  },
  getters: {
    getFile: state => state.getFile,
    /*designFilez: state => {
      if (!state.designFile || !Object.entries(state.designMarkup).length) {
        return;
      }
      return getParentAndChild(state.designMarkup.children);
    },*/
    siteUrl: state => state.siteUrl,
    siteUrlProxy: state => {
      return config.serverUrl + "/proxy/" + state.siteUrl;
    },
    viewerReady: state => {
      return state.getFile && state.siteUrl;
    },
    errors: state => state.errors
  },
  mutations: {
    SET_DESIGN(state, payload) {
      state.getFile = payload;
    },
    SET_SITE_URL(state, payload) {
      state.siteUrl = payload;
    },
    SET_ERRORS(state, payload) {
      state.errors = payload;
    }
  },
  actions: {
    setDesign({ commit }, payload) {
      commit("SET_DESIGN", payload);
    },
    setSiteUrl({ commit }, payload) {
      const url = "http://" + payload.replace("http://", "").replace("https://", "");
      return new Promise(resolve => {
        commit("SET_SITE_URL", url);
        resolve(url)
      });
    },
    setErrors({ commit }, payload) {
      commit("SET_ERRORS", payload);
    }
  }
});

