<template>
  <div
    v-if="siteUrl"
    class="site-viewer"
    :class="{
      hidden: !siteUrl || siteUrl === '',
      active: viewActive
    }"
  >
    <iframe
      width="100%"
      height="99%"
      sandbox="allow-same-origin allow-scripts"
    ></iframe>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SiteViewer",
  computed: {
    ...mapGetters(["siteUrl", "siteUrlProxy", "viewerReady"]),
    viewActive() {
      return this.viewerReady && this.$route.name === "view";
    }
  },
  methods: {
    displayErrorTip: function(node, errors) {
      if (!node || !errors || !errors.length) {
        return;
      }
      const self = this;
      const left = node.offsetLeft + node.clientWidth / 2;
      const top = node.offsetTop + node.clientHeight - 7;
      const point = document.createElement("div");
      const renderErrors = () => {
        let errorsHtml = "";
        errors.forEach(error => {
          errorsHtml += `<li><b>${error.name}: </b>${error.error}px</li>`;
        });
        return errorsHtml;
      };
      point.setAttribute("class", "lky-error-tip");
      point.setAttribute("style", `left: ${left}px; top: ${top}px`);
      point.innerHTML = `
      <div class="lky-point"></div>
        <div class="lky-popup">
          ${
            this.text.errorsFound
          }: <ul class="lky-errors">${renderErrors()}</ul>
        </div>
      </div>`;
      document.body.appendChild(point);
      const pointElem = point.querySelector(".lky-point");
      const popupElem = point.querySelector(".lky-popup");
      pointElem.onmouseover = () => {
        addClass(popupElem, "active");
        self.elementHighlight(node);
      };
      pointElem.onmouseleave = () => {
        removeClass(popupElem, "active");
        self.removeHighlight();
      };
    },
    elementHighlight(node) {
      if (!node) {
        return;
      }
      const styles = node.getBoundingClientRect();
      let div = document.createElement("div");
      div.className = "node-highlight";
      div.style.position = "absolute";
      div.style.content = "";
      div.style.height = `${styles.height + "px"}`;
      div.style.width = `${styles.width + "px"}`;
      div.style.top = `${styles.top + "px"}`;
      div.style.right = `${styles.right + "px"}`;
      div.style.bottom = `${styles.bottom + "px"}`;
      div.style.left = `${styles.left + "px"}`;
      div.style.background = "#05f";
      div.style.opacity = "0.25";
      document.body.appendChild(div);
    },
    removeHighlight() {
      const elems = document.getElementsByClassName("node-highlight");
      for (let elm of elems) {
        document.body.removeChild(elm);
      }
    }
  }
};
</script>

<style lang="scss">
.site-viewer {
  overflow-x: hidden;
  transition: opacity 1s 2s linear;
  &.hidden {
    opacity: 0;
  }
  &:not(.active) {
    &:before {
      content: "";
      position: absolute;
      background-color: rgba(0, 0, 0, 0.8);
      z-index: 1;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
    }
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: -1;
  }

  .lky-error-tip {
    position: absolute;
    z-index: 101;
    .lky-point {
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0);
      width: 14px;
      height: 14px;
      background-color: red;
      border: 4px solid #fff;
      border-radius: 50%;
      z-index: 101;
    }
    .lky-popup {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #fff;
      padding: 20px;
      width: 0;
      border-radius: 5px;
      visibility: hidden;
      &.active {
        visibility: visible;
        width: 250px;
        height: auto;
      }
    }
  }
  .node-highlight {
    z-index: 100;
  }

  &:not(.active) {
    .lky-error-tip {
      display: none;
    }
  }
}
</style>
