import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import View from "./views/View.vue";

import TestView from "./views/TestView";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/view",
      name: "view",
      component: View
    },
    {
      path: "/test-view",
      name: "test-view",
      component: TestView
    }
  ]
});
