<template>
  <div class="design-uploader" v-show="!file">
    <label for="file" class="drag-area"
      >Drag the design or click for upload</label
    >
    <input id="file" type="file" ref="file" @change="upload" hidden />
  </div>
</template>
<script>
export default {
  name: "DesignUploader",
  data: () => ({
    file: null
  }),
  methods: {
    upload(e) {
      const file = e.target.files[0] || e.originalEvent.dataTransfer.files[0];
      if (!file) {
        return;
      }
      this.file = file;
      this.$store.dispatch("setDesign", file);
      /** ToDo: Add file type validation **/
    }
  }
};
</script>

<style lang="scss" scoped>
.design-uploader {
  .drag-area {
    background-color: $color-bg2;
    border: 1px dashed $color-w1;
    border-radius: 7px;
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    &:hover {
      opacity: 0.8;
    }
  }
}
</style>
