<template>
  <div class="enter-website">
    <form-input
      name="url"
      ref="url"
      label="Enter website:"
      placeholder="Type website url, like google.com"
      @onchange="checkUrlAvailable"
    />
  </div>
</template>

<script>
import config from "../config";
import axios from "axios";
import { mapGetters } from "vuex";
import _ from "lodash";
import FormInput from "../atoms/FormInput";
export default {
  name: "EnterWebsite",
  components: { FormInput },
  computed: {
    ...mapGetters(["siteUrl"])
  },
  methods: {
    checkUrlAvailable: _.throttle(function() {
      const url = this.$refs.url;
      if (!url.valid && url.inputValue !== "") {
        return;
      }

      const self = this;
      this.$store.dispatch("setSiteUrl", url.inputValue).then(url => {
        self.$nextTick(() => {
          const iframe = document.getElementsByTagName("iframe")[0];
          const loadHTML = function(html) {
            iframe.src = "about:blank";
            iframe.contentWindow.document.open();
            iframe.contentWindow.document.write(
              html.replace(/<head>/i, '<head><base href="' + url + '">')
            );
            iframe.contentWindow.document.close();
          };
          axios.get(`${config.serverUrl}/proxy/${url}`).then(r => {
            loadHTML(r.data);
          });
        });
      });
    }, 300)
  }
};
</script>

<style lang="scss" scoped>
.enter-website {
  margin: 60px 0 40px;
}
</style>
