import axios from "axios";
import config from "./config";
import {stringify} from 'flatted/esm';

export function getErrors(designFile, nodes, cb) {
  const options = {
    headers: {
      "Content-Type": "multipart/form-data"
    }
  };

  const array = [...nodes];
  const json =  stringify(array);
  const formData = new FormData();
  formData.append("file", designFile);
  formData.append("nodes", json);
  axios
    .post(config.serverUrl + "/get-errors", formData, options)
    .then(errors => {
      cb(errors);
    });
}

export function getTargetView(url, cb) {
  axios.get(config.serverUrl + "/get-target-view" + "?url=" + url).then(res => {
    const html = res.data;
    cb(html);
  });
}

